.homecontainer {
  background-image: url("../imgs/home/back.png") !important;
  height: 100vh;
  width: 100%;
  background-size: cover;
}
.footer {
  position: absolute;
  bottom: -45px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;

  height: 120px;
}

.footerimg {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
}

.footer button {
  position: relative;
  z-index: 2;
  background: transparent; /* Optional: make button background transparent */
  border: none; /* Optional: remove border */
  padding: 10px; /* Adjust padding as needed */
  cursor: pointer; /* Add a pointer cursor */
  width: 100px;
  height: 70px;
  background-color: transparent;
}

.footer button:hover {
  /* Optional: Add hover effect */
  background-color: rgba(255, 255, 255, 0.3); /* Example hover effect */
}
